
import { ref, defineComponent, onMounted } from "vue";
import { mapActions, mapState } from "vuex";
import useVuelidate from "@vuelidate/core";
import Status from "@/enums/StatusEnum";
import AddTaskModal from "@/components/AddTaskModal.vue";
import TaskDetailsModal from "@/components/TaskDetailsModal.vue";
import SearchTask from "@/components/SearchTask.vue";

export default defineComponent({
  name: "Tasks",
  components: {
    AddTaskModal,
    TaskDetailsModal,
    SearchTask,
  },
  setup() {
    // Before the component is mounted, the value
    // of the ref is `[]` which is the default
    let num = 0;
    const divs = ref([]);
    onMounted(() => {
      divs.value.forEach((element) => {
        setTimeout(() => {
          element.classList.add("animation");
          console.log(element);
        }, num);
        num += 500;
      });
    });
    return {
      divs,
      close,
    };
  },
  data() {
    return {
      v$: useVuelidate(),
      title: "",
      description: "",
      time: "",
      isShow: false,
      isShowChange: false,
      currentTask: {},
      Status,
      sortedProducts: [],
      searchValue: "",
    };
  },
  methods: {
    ...mapActions(["SET_TASKS", "CREATE_NEW_TASK", "DELETE_TASK"]),
    setSearchValue(data) {
      this.searchValue = data.title;
    },
    removeAnimation(task) {
      setTimeout(() => {
        task.isNew = false;
      }, 2000);
    },
    showNew() {
      this.isShow = true;
      console.log(this.isShow);
    },
    showChange(task) {
      this.currentTask = task;
      this.isShowChange = true;
    },
    removeEditTask() {
      this.isShowChange = false;
    },
    removeNew() {
      this.isShow = false;
    },
    deleteTask(id) {
      this.DELETE_TASK(id);
    },
    sortProductsBySearchValue(value) {
      this.sortedProducts = [...this.tasks];
      if (value) {
        this.sortedProducts = this.sortedProducts.filter(function (item) {
          return item.title.toLowerCase().includes(value.toLowerCase());
        });
        console.log(this.sortedProducts);
      } else {
        this.sortedProducts = this.tasks;
      }
    },
  },
  watch: {
    searchValue: function () {
      this.sortProductsBySearchValue(this.searchValue);
    },
  },
  computed: {
    ...mapState(["tasks"]),
    filteredProducts() {
      if (this.sortedProducts.length) {
        return this.sortedProducts;
      } else {
        return this.tasks;
      }
    },
  },
  mounted() {
    this.sortProductsBySearchValue(this.searchValue);
    console.log(this.searchValue);
  },
});
